import React, {Component} from 'react';
import {connect} from 'react-redux'
import CardComponent from "../card-component/card.component";
class SearchContentComponent extends Component {
    render() {
        const {search} = this.props
        const filter = search.filter((x)=>{return x.title.includes(this.props.term)} )

        return (
            <React.Fragment>
                <div className="row" style={{paddingTop: "50px"}}>
                    {filter.map((card)=><div className="col">
                        <CardComponent key = {card.id} props={card}/>
                    </div>)}
                </div>
            </React.Fragment>
        );
    }
}
const mapStateToProp = (state) =>({
    search: state.cards,
    term: state.search
})
export default connect(mapStateToProp)(SearchContentComponent);
