import React, {Component} from 'react';
import './sidebar.style.css'
import {connect} from 'react-redux'
import TabContentNotesComponent from "../tabcontent-notes-component/tabcontent-notes.component";
import ReminderComponent from "../reminder-component/reminder.component";
import ArchiveComponent from "../archive-component/archive.component";
import BinComponent from "../bin-component/bin.component";
const dark = {
    color: '#ffffff'
}
const bright = {
    color: '#000000'
}
const darkI = {
    color: '#ffffff',
    fontSize: '20px',
    margin: '-5px 5px 5px 5px',
}
    const brightI = {
    color: '#000000',
        fontSize: '20px',
        margin: '-5px 5px 5px 5px',
}

class SidebarComponent extends Component {
    constructor(props) {
        super(props);
        this.state = {
            toggle: false
        }
    }
    handleClick = () =>{
        this.props.toggleFun()
    }
    render() {
        const ui = this.props.theme
        return (
            <React.Fragment>
                <div style = {{display : 'flex'}}>
                         <div className={this.props.toggle?'sidebar toggle': 'sidebar'}>
                             <h1 className="hide" style={{borderBottom: "1px solid #d9d9d9", justifyContent: 'space-between', padding: "10px 0"}}>Pin it<span style={{fontSize: '10px'}}>
                                 <div className="hide" onClick={()=>{this.handleClick()}}>
                                     <i style={{fontSize: "20px", padding: "5px", color: "#d9d9d9"}} className='fas fa-bars'></i>
                                 </div></span></h1>
                             <div className="sidebar-section">
                                 <button style={ui?dark:bright} onClick={(e)=>this.currentTab(e, 'notes')} className="sidebar-tab-selection active-tab"><i className='fas fa-sticky-note' style={ui?darkI:brightI}>

                            </i>Notes</button>
                        <button style={ui?dark:bright} onClick={(e)=>this.currentTab(e, 'reminder')} className="sidebar-tab-selection">
                            <i className='fas fa-bell' style={ui?darkI:brightI}>
                            </i>Reminders</button>

                    {/*    /!*ToDo: Map the labels here*!/*/}

                        <button style={ui?dark:bright} onClick={(e)=>this.currentTab(e, 'archive')} className="sidebar-tab-selection">
                            <i className='fas fa-archive' style={ui?darkI:brightI}>
                            </i>Archive</button>
                        <button style={ui?dark:bright} onClick={(e)=>this.currentTab(e, 'bin')} className="sidebar-tab-selection">
                            <i className='fas fa-recycle' style={ui?darkI:brightI}>
                            </i>Bin</button>
                    </div>
                </div>
                    <div className="content-main" style={{width: '100%'}}>
                        <div className="content active" style={{ width: '100%'}} id = 'notes'>
                            <TabContentNotesComponent/>
                        </div>
                        <div className="content" id = 'reminder'>
                            <ReminderComponent/>
                        </div>
                        <div className="content" id = 'archive'>
                            <ArchiveComponent/>
                        </div>
                        <div className="content" id = 'bin'>
                            <BinComponent/>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        );
    }

    currentTab = (e, id) =>{
        let contents = document.getElementsByClassName('content')
        for(let i = 0; i < contents.length; i++){
            contents[i].style.display = 'none'
        }
        let select_tab = document.getElementsByClassName('sidebar-tab-selection')
        for(let i = 0; i < select_tab.length; i++){
            select_tab[i].className = select_tab[i].className.replace("active-tab", '')
        }
        e.currentTarget.className += ' active-tab'
        document.getElementById(id).style.display = 'block'
        console.log(e.currentTarget.className, e.currentTarget.id)
    }
}
const mapStateToProp = (state) =>({
    cards: state.cards,
    theme: state.theme
})

export default connect(mapStateToProp)(SidebarComponent);
