import React from 'react';
import { useSelector, useDispatch } from "react-redux";

import {setToggleTheme} from "../../redux/theme/theme.action";

import HamburgerMenuComponent from "../hamburger-menu-component/hamburger-menu.component";
import SearchBarComponent from "../search-bar-component/search-bar.component";

import './navbar.style.css'

const NavbarComponent = ({toggle, theme}) => {
    let handleClick = () =>{
        toggle()
    }
    const ui = useSelector(state => state.theme)
    const dispatch = useDispatch()
    return (
        <React.Fragment>
            <div className="navbar">
                <div style = {{display: "flex", width: "200px"}}>
                    <div className="navbar-hamburger">
                        <button onClick={()=>{handleClick()}}><HamburgerMenuComponent/></button>
                    </div>
                    <h4 className="title">Pin<span style={{color: 'grey'}}>IT</span></h4>
                </div>
                <div className= "navbar-search-container">
                    <SearchBarComponent/>
                </div>
                <button onClick={() =>dispatch(setToggleTheme())}><h4><i style = {ui?{color:'#ffffff', fontSize: '30px', transition: '0.5s'}:{color: '#000000', fontSize: '30px', transition: '0.5s'}} className="fas fa-adjust"></i>
                </h4></button>
            </div>
        </React.Fragment>
    )
}
export default NavbarComponent;
