import React, {Component} from 'react';
import {connect} from 'react-redux'
import CardComponent from "../card-component/card.component";
import AddCardComponent from "../add-card-component/add-card.component";
import './tabcontent-notes.style.css'
import SearchContentComponent from "../search-content-component/search-content.component";

class TabContentNotesComponent extends Component {
    componentDidMount() {
        console.log('localstorage', JSON.stringify(localStorage.getItem('persist:root')))
    }

    render() {
        const {cards} = this.props
        const notes = cards.filter((note)=>{
            return note.category === 'notes' && note.pinned === false && note.archive === false
        })
        const pinned = cards.filter((note)=>{
            return note.category === 'notes' && note.pinned === true && note.archive === false
        })
        return (
            <>
                {this.props.active?<SearchContentComponent/>:
                    <div className="notes-tab-content">
                        <div style={{display: "flex", justifyContent:'center', padding: "20px"}}>
                            <AddCardComponent/>
                        </div>
                        <div className="row">
                            {pinned.length > 0?<p>Pinned</p>:<p></p>}
                        </div>
                        <div className = "row cards-container" style={pinned.length>0?{marginBottom: '50px'}:{}}>
                            {pinned.map((x) =>
                                <div className="col cards-container">
                                    <CardComponent key = {x.id} props = {x}/>
                                </div>)}
                        </div>
                        <div className="row">
                        </div>
                        <div className="row cards-container">
                            {pinned.length > 0?<p>Others</p>:<p></p>}
                        </div>
                        <div className = "row cards-container">
                            {notes.map((x) =>
                                <div className="col cards-container">
                                    <CardComponent key = {x.id} props = {x}/>
                                </div>)}
                        </div>
                    </div>}
            </>
        );
    }
}
const mapStateToProp = state =>({
    cards: state.cards,
    active: state.active
})

export default connect(mapStateToProp)(TabContentNotesComponent);
