import React, {useState} from 'react';
import {taskDone, taskNotDone} from "../../redux/cards/cards.action";
import './card.style.css'
import {useSelector, useDispatch} from "react-redux";
import {pinCard, pinArchive,pinReminder,pinBin} from "../../redux/cards/cards.action";
import Modal from "../modal-component/modal.component";

const CardComponent = (props) =>{
    const {id, title, content,type, pinned,archive,bin,reminder,time, category, files} = props.props

    const [hover, setHover] = useState(false)
    const [modal, setModal] = useState(false)
    const [check, setCheck] = useState(false)
    const [cId, setCId] = useState('')
    const onHoverEvent = () =>{
        setHover(true)
    }
    const onHoverLeaveEvent = () =>{
        setHover(false)
    }
    const hide = {
        opacity: '0',
        transition: '0.5s'
    }
    const show = {
        opacity: '1',
        transition: '0.5s'
    }
    const darkI = {
        color: "#595959"
    }
    const brightI = {
        color: '#000000'
    }
    const showModal = () =>{
        setCId(id)
        setModal(true)
    }
    const closeModal = () =>{
        setModal(false)
    }
    const handleCheckbox = (e,tid) =>{
        console.log('check',e.target.checked)
        if(e.target.checked){
            dispatch(taskDone(id,tid))
            setCheck(true)
        }
        else{
            dispatch(taskNotDone(id,tid))
            setCheck(false)
        }
    }
    const ui = useSelector(state =>state.theme)
    const dispatch = useDispatch()
    return (
        <React.Fragment>
            <Modal className = "modal"
                   show = {modal}
                   close = {closeModal}
                   time = {time}
                   title = {title}
                   content = {content}
                   id = {cId}
            >
            </Modal>
            <div style={{backgroundImage: `url(${files})`}} id = "myCard" className="card" onMouseLeave={() =>{onHoverLeaveEvent()}} onMouseEnter={()=>{onHoverEvent()}}>
                <div className="card-data">
                    <h6>{title}</h6>
                {/*    Todo: Logic will come here*/}
                    {type === 'list'?
                        <ul>{content.map((x)=>
                            <li key={id}><input style = {{marginRight: '10px', marginTop: '5px'}} type="checkbox" onChange={(e)=>{handleCheckbox(e,x.id)}} defaultChecked={x.status ==='complete'?true:false}/>
                                <span style={x.status === 'complete'?{textDecoration: 'line-through'}:{textDecoration: 'none'}}>
                                    {x.data}
                                </span>
                            </li>)}
                        </ul>:<p>{content}</p>}
                </div>
                <div style ={hover?show:hide} className="card-options">

                    <button className="tooltip" style={{backgroundColor: 'transparent', border: '0'}}
                        onClick={()=>dispatch(pinReminder(id))}>
                        <div className="tool">Reminder</div>
                        <i className='fas fa-bell icons' style={ui?darkI:brightI}>
                        </i>
                    </button>
                    {pinned?'':<button className="tooltip" style={{backgroundColor: 'transparent', border: '0'}}
                                        onClick={()=>dispatch(pinCard(id))}>
                        <div className="tool">Pin</div>
                        <i className='fas fa-map-pin icons' style={ui?darkI:brightI}>
                        </i>
                    </button>}
                    <button className="tooltip" style={{backgroundColor: 'transparent', border: '0'}}
                        onClick={()=>{dispatch(pinArchive(id))}}>
                        <div className="tool">Archive</div>
                        <i className='fas fa-file-archive icons' style={ui?darkI:brightI}>
                        </i>
                    </button>
                    {type === 'para'?<button className="tooltip" style={{backgroundColor: 'transparent', border: '0'}}
                                             onClick={()=>showModal()}>
                        <div className="tool">Edit</div>
                        <i className='fas fa-pen icons' style={ui?darkI:brightI}>
                        </i>
                    </button>:''
                    }
                    {category==='notes'?<button className="tooltip" style={{backgroundColor: 'transparent', border: '0'}}
                                                onClick={() =>dispatch(pinBin(id))}>
                        <div className="tool">Delete</div>
                        <i className="fas fa-trash-alt icons" style={ui?darkI:brightI}></i>
                    </button>: <button className="tooltip" style={{backgroundColor: 'transparent', border: '0'}}
                                       onClick={() =>dispatch(pinBin(id))}>
                        <div className="tool">Recycle</div>
                        <i className=" fas fa-recycle icons" style={ui?darkI:brightI}></i>
                    </button>}
                </div>
                {pinned?<div className="card-pinned">
                    <button style={{backgroundColor: 'transparent', border: '0'}}
                             onClick={()=>dispatch(pinCard(id))}>
                        <i className='fas fa-map-pin icons' style={ui?darkI:brightI}></i>
                    </button>
                </div>:''}
                <div></div>
            </div>
        </React.Fragment>
    );
}
export default CardComponent;
