import React from 'react';
import {useSelector} from "react-redux";
import CardComponent from "../card-component/card.component";
import SearchContentComponent from "../search-content-component/search-content.component";

const ReminderComponent = () =>{
    const cards = useSelector(state =>state.cards)
    const active = useSelector(state => state.active)
    const reminder = cards.filter((card) => {
        return card.reminder === true && card.category === 'notes'
    })
    return(
        <>{active?<SearchContentComponent/>:<>
            <div className="row">
                <h2>Archive</h2>
            </div>
            <div className="row cards-container">
                {reminder.map((card)=><div className="col cards-container">
                    <CardComponent key = {card.id} props = {card}/>
                </div>)}
            </div>
            </>}
        </>
    )
}

export default ReminderComponent;
