import {ADD_CARDS, ARCHIVE, BIN, PIN, REMINDER, TASKDONE, TASKNOTDONE, UPDATE} from "../constants";

const INITIAL_STATE = []

function addCardsReducer(state = INITIAL_STATE, action){
    switch (action.type) {
        case ADD_CARDS:
            return [...state, action.data]
        case PIN:
            return state.map(
                (card)=>{
                    if(card.id === action.id){
                        return {...card, pinned: !card.pinned}
                    }
                    else {
                        return {...card}
                    }
                }
            )
        case ARCHIVE:
            return state.map((card)=>{
                if(card.id === action.id){
                    return {...card, archive: !card.archive}
                }
                else{
                    return {...card}
                }
            })
        case BIN:
            return state.map((card)=>{
                if(card.id === action.id){
                    if(card.category === 'notes'){
                        return {...card, category: 'bin'}
                    }
                    else{
                        return {...card, category: 'notes'}
                    }
                }
                else{
                    return {...card}
                }
            })
        case REMINDER:
            return state.map((card)=>{
                if(card.id === action.id){
                    return {...card, reminder: !card.reminder}
                }
                else{
                    return {...card}
                }
            })
        case TASKDONE:
            return state.map((card) =>{
                if(card.id === action.id){
                    card.content.map((task) =>{
                        if(task.id === action.task_id){
                            task.status = 'complete'
                        }
                    })
                };
                return {...card}
            })
        case TASKNOTDONE:
            return state.map((card) =>{
                if(card.id === action.id){
                    card.content.map((task) =>{
                        if(task.id === action.task_id){
                            task.status = 'incomplete'
                        }
                    })
                };
                return {...card}
            })

        case UPDATE:
            return state.map((card)=>{
                if(card.id === action.id){
                    return{...card, title: action.data.title, content: action.data.content}
                }
                else{
                    return {...card}
                }
            })
        default:
            return state;
    }
}
export default addCardsReducer

