import React from "react";
import {useSelector} from "react-redux";
import CardComponent from "../card-component/card.component";
import SearchContentComponent from "../search-content-component/search-content.component";

const ArchiveComponent = () =>{
    const cards = useSelector(state =>state.cards)
    const active = useSelector(state =>state.active)
    const archives = cards.filter((card)=>{
        return card.archive === true && card.category === 'notes'
    })
    return(
        <React.Fragment>
            {active?<SearchContentComponent/>:<>
            <div className="row">
                <h2>Archive</h2>
            </div>
            <div className="row cards-container">
                {archives.map((card)=>
                    <div className="col cards-container">
                        <CardComponent props = {card}/>
                    </div>
                )}
            </div></>}
        </React.Fragment>
    )
}
export default ArchiveComponent
