import React, {useState} from 'react';
import {useDispatch} from "react-redux";
import {updateCard} from "../../redux/cards/cards.action";
import './modal.style.css';

const Modal = (props) => {
    const dispatch = useDispatch()
    const [card, setCard] = useState({title: props.title, content : props.content})
    const handleSubmit = () =>{
        dispatch(updateCard(props.id, card))
        props.close()
    }
    return (
        <div>
            <div className="modal-wrapper"
                 style={{
                     transform: props.show ? 'translateY(0vh)' : 'translateY(-100vh)',
                     opacity: props.show ? '1' : '0'
                 }}>
                <div className="modal-content">
                    <div className="modal-header">
                        <h5>{props.time}</h5>
                    </div>
                    <div className="modal-body">
                        <p style={{color: 'red'}}>
                            <input className="modal-input" defaultValue={props.title} value={card.title}
                            onChange={e =>{
                                const val = e.target.value;
                                setCard(prevState => {
                                    return{...prevState, title: val}
                                })
                            }}/>
                        </p>
                        <p>
                            <input className="modal-input" defaultValue={props.content} value={card.content}
                            onChange={e=>{
                                const vl = e.target.value;
                                setCard(prevState => {
                                    return{...prevState, content: vl}
                                })
                            }}/>
                        </p>
                    </div>
                    <div className="modal-buttons"><button className="save" onClick={() =>{handleSubmit()}}>Submit</button><button onClick={props.close} className="close">Close</button></div>
                </div>
            </div>
        </div>
    )
}

export default Modal;
