import React, {Component} from 'react';
import './search-bar.style.css'
import {setActive, deactivate} from "../../redux/active/active.action";
import {setSearch} from "../../redux/search/search.action";
import {connect} from 'react-redux'
class SearchBarComponent extends Component {
    state = {
        term: ''
    }
    handleChange = (e) =>{
        console.log(e.target.value.length > 0)
        if(e.target.value.length > 0){
            this.props.active();
        }
        else{
            this.props.deActive()
        }
    }
    render() {
        return (
            <React.Fragment>
                <div className="search-bar">
                    <input onChange={(e)=>
                           {this.setState({term:e.target.value},
                               ()=>{this.props.search(this.state.term)}); this.props.active(); this.handleChange(e)}}
                           placeholder="Search" value={this.state.term}/>
                           <p style={this.state.term.length>2?{color: 'red', margin: 'auto', cursor: 'pointer'}:{display: 'none'}} onClick={()=>{this.setState({term:''},()=>this.props.search(this.state.term));}}>X</p>
                </div>
            </React.Fragment>
        );
    }
}

const mapDispatchToProp = dispatch =>({
    search: term =>dispatch(setSearch(term)),
    active: () =>dispatch(setActive()),
    deActive: () => dispatch(deactivate())
})
export default connect(null,mapDispatchToProp)(SearchBarComponent);
