import React, {Component} from 'react';
//Import libraries
import {connect} from "react-redux";
//Import Component
import {addCard} from "../../redux/cards/cards.action";

import './add-card.style.css'
import img1 from './notes.jpg'
//Declare random id
var uniqid = require('uniqid')
var num = 100
class AddCardComponent extends Component {
    constructor(props) {
        super(props);
        this.state = {
            list_count: [0],
            displayAddPara: false,
            displayAddList: false,
            card : initialState(),
            list_active: false,
            list: [],
            delete_list: [],
            image:''
        }
    }
    render() {
        const {title} = this.state
        return (
            <React.Fragment>
                <div className="add-card">
                    {this.listen}
                    <div style={{display: 'flex'}}>
                        {this.state.displayAddList?
                            <input onChange={
                                (e)=>
                                {this.state.card.type = 'list'; this.handleChange(e)}}
                                   id = "title" value={title} placeholder="Add a title" autoComplete={false}
                            />:
                            <input id = "title" value={this.state.card.title}
                                   onFocus={(e)=>this.focus(e,'para')}
                                   type="text"
                                   autoComplete={false}
                                   placeholder="Type a note"
                                   onChange={(e)=>{this.state.card.type='para';this.handleChange(e)}
                            }/>}
                        <div className="add-card-images">
                            {/*If Add para is active , Add list will hide*/}
                            <div className="add-card-tick">
                                {this.state.displayAddPara || this.state.displayAddList?'':<button onClick={(e)=>{this.focus(e,'list')}}><i className='fas fa-check-square'></i></button>}
                            </div>
                        </div>
                    </div>
                    {/*If addPara is opened, this div will be shown*/}
                    <div style={this.state.displayAddPara?{display: 'block', transition: '1s'}:{display: 'none', transition: '1s'}} className="add-note-card">
                        <input
                            placeholder="make a note" id = "content"
                            value={this.state.card.content}
                            onChange={(e) => this.handleChange(e)} type = "text"
                        />
                        {this.addCardButtons('para')}
                    </div>
                    {/*If addList is opened, this div will be shown*/}
                    <div style={this.state.displayAddList?{display: 'block'}:{display: 'none'}} className="add-list-card">
                        <div id = 'list-input' style={{paddingLeft: '10px'}}>
                            {/*<span>+</span>*/}
                            <input
                                onKeyDown={(e)=>{this.keyDown(e); if(e.key === 'Enter'){this.pushIntoList(e)}}}
                                id = "99"
                                placeholder="+add task"
                            />
                        </div>
                        {this.addCardButtons('list')}
                    </div>
                    <img src={img1}/>
                </div>
            </React.Fragment>

        );
        
    }

    keyDown = (e)=>{
        if(e.key === 'Enter'){
            let inp = document.createElement('INPUT')
            let br = document.createElement('BR')

            inp.setAttribute('id', num)
            this.setState({delete_list:[...this.state.delete_list, num]})
            inp.setAttribute('placeholder', "+ add task")

            document.getElementById('list-input').appendChild(br)
            document.getElementById('list-input').appendChild(inp)
            document.getElementById(num).focus()
            document.getElementById(num).onkeydown = (e) =>{this.callf(e);if(e.key === 'Enter'){this.pushIntoList(e)}}
            num++
        }
    }
    callf = (e) =>{
        this.keyDown(e)
    }
    pushIntoList = (e) =>{
        this.setState({list_active: true})
        const temp = {id :e.target.id,data: e.target.value, status: 'incomplete'}
        let arrValue = this.state.list.concat(temp);
        this.setState({ list: arrValue},() => console.log('added list', this.state.list))
    }
    focus = (e, type)=>{
        type === 'para'?this.setState({displayAddPara: true}):this.setState({displayAddList: true});
    }
    blur = (e, type) =>{
        type === 'para'?this.setState({displayAddPara: false}):this.setState({displayAddList: false})
        this.setState({list_active: false})
    }
    addCardButtons(type) {
        return <>
            <div style={{display: 'flex', justifyContent: 'space-around'}}>
                <button
                    style={{backgroundColor: 'transparent', border: '0'}}
                    onClick={() => this.state.card.reminder = !this.state.card.reminder}>
                    <span className="tooltip"><i className='fas fa-bell icons'></i><span className="tool">Reminder</span></span>
                </button>
                <button
                    style={{backgroundColor: 'transparent', border: '0'}}
                    onClick={() => this.state.card.pinned = !this.state.card.pinned}>
                    <span className="tooltip"><i className='fas fa-map-pin icons'></i><span className="tool">Pin</span></span>
                </button>

                <button
                    style={{backgroundColor: 'transparent', border: '0'}}
                    onClick={() => this.state.card.archive = !this.state.card.archive}>
                    <span className="tooltip"><i className='fas fa-file-archive icons'></i><span className="tool">Archive</span></span>
                </button>
            </div>
            <div style={{display: 'flex', justifyContent: 'flex-end'}}>
                {type === 'para'?<button
                   onClick={() => this.handleSubmit(this.state.card, type)}
                   className="add-card-save-button">save</button>:<span className="instruction"><button disabled={this.state.list.length=== 0?'true':false}
                    onClick={() => this.handleSubmit(this.state.card, type)} className="add-card-save-button">save</button><span className={this.state.list.length>0?'denter':'enter'}>please add one task</span></span> }
                <button className="add-card-close-button" onClick={(e)=>{this.blur(e,type)}}>close</button>
            </div>
        </>;
    }
    handleChange = e =>{
        const {card} = {...this.state}
        const cardT = card
        const {id, value} = e.target
        cardT[id] = value
        this.setState({card: cardT})
    }

    handleSubmit = (data, type) =>{
        this.state.list_active?this.state.card.content = this.state.list:console.log('not')
        this.state.card.id = uniqid()
        let time = Date()
        this.state.card.time = time.substring(16,21)
        this.props.addCards(data)
        this.setState({card: initialState()}, ()=>console.log('callbackkkkkkkkkk', this.state))
        this.blur(null,type)
        this.setState({list:[]}, ()=>console.log('hellllo', this.state.list))
        this.state.delete_list.map((x)=>{
            let element = document.getElementById(x)
            element.parentNode.removeChild(element)
        })
        this.setState({delete_list: []})
        document.getElementById('99').value = ''
    }
}
const mapDispatchToProp = dispatch =>({
    addCards: data => dispatch(addCard(data))
})

const initialState = () =>{
    return {
        id: '',
        title: '',
        type: '',
        content: '',
        time: '',
        image:'',
        category: 'notes',
        pinned: false,
        archive: false,
        reminder: false,
        bin: false
    }
}

export default connect(null, mapDispatchToProp)(AddCardComponent);
