import {ADD_CARDS, ARCHIVE, BIN, REMINDER, TASKDONE, TASKNOTDONE, UPDATE} from "../constants";
import {PIN} from "../constants";

export const addCard = (data) =>{
    return{
        type: ADD_CARDS,
        data
    }
}
export const pinCard = (id) =>{
    return{
        type: PIN,
        id
    }
}
export const pinArchive = (id) =>{
    return{
        type: ARCHIVE,
        id
    }
}
export const pinReminder = (id)=>{
    return{
        type: REMINDER,
        id
    }
}
export const pinBin = (id)=>{
    return{
        type: BIN,
        id
    }
}
export const taskDone = (id, task_id)=>{
    return{
        type: TASKDONE,
        id,
        task_id
    }
}
export const taskNotDone = (id, task_id) => {
    return {
        type: TASKNOTDONE,
        id,
        task_id
    }
}
export const updateCard = (id, data)=>{
    return {
        type: UPDATE,
        id,
        data
    }
}
