import React from "react";
import {useSelector} from "react-redux";
import CardComponent from "../card-component/card.component";

const BinComponent = () =>{
    const cards = useSelector(state =>state.cards)
    const bin = cards.filter((cards) =>{
        return cards.category === 'bin'
    })
    return(
        <React.Fragment>
            <div className="row">
                <h2>Recycle Bin</h2>
            </div>
            <div className="row">
                {bin.map((card)=><div className="col">
                    <CardComponent props = {card}/>
                </div>)}
            </div>
        </React.Fragment>
    )
}
export default BinComponent
